.card {
    width: 25rem;
    box-shadow: 0 3px 5px rgb(214, 214, 214);
    border: none;
    transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    -moz-transition: all .4s ease-in-out;
    -ms-transition: all .4s ease-in-out;
    -o-transition: all .4s ease-in-out;
}

.card:hover {
    box-shadow: 0 5px 5px steelblue;
    cursor: pointer;
}

.cards {
    float: left;
    margin-top: 30px;
    
}

.card-img-top {
    width: 190px;
    height: 200px;
    margin: 0 auto;
}

.card:hover .card-body {
    margin: 0;
}

.card-body {
    background-color: rgb(239, 243, 243);
    padding-bottom: 1px;
}

.card-title {
    margin-bottom: 1px;
}

.card-text {
    margin-bottom: 2px;
}

.card-body .btn {
    margin-bottom: 5px;
}