.filter-heading {
    color: rgb(38, 38, 119);
    font-weight: bold;
    font-size: 15px;
    margin-bottom: 10px;
    margin-top: 20px;
}

.filter-subheading {
    color: rgb(67, 67, 122);
    font-weight: bold;
    font-size: 12px;
    margin-bottom: 10px;
    margin-top: 20px;
}

.select {
    padding: 5px;
    width: 90%;
    outline: none;
    border: none;
    box-shadow: 0 2px 3px rgb(163, 162, 162);
    color: rgb(158, 155, 155);
    font-size: 15px;
}

label {
    margin-left: 5px;
    font-size: 15px;
}

input[type="checkbox"]:checked + label  {
    color: rgb(134, 32, 18);  
}

input[type="checkbox"] + label  {
    color: rgb(131, 130, 130);
}

input[type="radio"]:checked + label  {
    color: rgb(134, 32, 18);
}

input[type="radio"] + label  {
    color: rgb(131, 130, 130);
}