* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 10px;  
}

html, body {
    max-width: 100%;
    overflow-x: hidden;
}


body a:link,
body a:active,
body a:visited{
    text-decoration: none;
}

#time {
    font-size: 1rem;
    color: steelblue;
}

/* ************************************* NAVBAR 1 *************************************************** */

.navbar1 {
    background-color: white;
    border-bottom: 1px solid steelblue;
}


.navbar-brand img {
    width: 90px;
    height: 50px;
    margin-left: 50px;
}

.navbar1 a{
    color: steelblue !important;
    font-weight: 600;
    font-size: 1.7rem;
}

.navbar-collapse a:hover {
    opacity: .7;
}

ion-icon {
    font-size: 1em;
    color: green;
    font-size: 2.5rem;
}

.notification ion-icon {
    color: rgb(189, 189, 10);
}